@import "src/styles/mixins/no-list-style";

.header{
	margin-bottom: 20px;
	margin-left: 20px;
}
.navList{
	@include no-list-style;
	display: flex;
	justify-content: flex-start;
}
.navList__item{
	&:not(:last-child){
		margin-right: 40px;
	}
}

.navList_active{
	text-decoration-line: underline;
}